	module.exports = {"data":{"site":{"siteMetadata":{"title":"aendrew.com v8","author":"Aendrew Rininsland"}},"markdownRemark":{"id":"/Users/andrew.rininsland/Projects/OTHER/aendrew.github.io/src/pages/2011-10-20-more-foi-requests/index.md absPath of file >>> MarkdownRemark","html":"<h3>An FOI request is like a journey…</h3>\n<ul>\n<li>It’s a many-step process, and while it might not go entirely smoothly, the more planning you do, the easier it’ll be.</li>\n<li>\n<p>Research</p>\n<ul>\n<li>\n<p>Check it anyone else is already compiling data</p>\n<ul>\n<li>Unions and other professional bodies, ombudsman, watchdog groups, researchers — all useful</li>\n<li>Disclosure logs first!</li>\n</ul>\n</li>\n<li>Try to find a context for your story</li>\n</ul>\n</li>\n<li>\n<p>Making the request</p>\n<ul>\n<li>Take screenshots; CC to self where applicable.</li>\n<li>Request receipt</li>\n<li>Remind of duty to advise and assist</li>\n<li>Note differences between a service and an authority — target your request appropriately!</li>\n</ul>\n</li>\n</ul>","frontmatter":{"title":"More on FOI requests","date":"October 20, 2011"}}},"pathContext":{"path":"content/more-foi-requests"}}

